@import url('https://fonts.googleapis.com/css?family=Dancing+Script|Kanit');

.Gallery-banner {
    background-image: url('../../images/home-background.jpg');
    height: 50vh;
    background-position: top;
    background-repeat: no-repeat;
    background-size: cover;
    text-align: center;
    color: #efecec;
    margin-bottom: 30px;
}

.Gallery-branding {
    font-weight: bold;
    padding-top: 8%;
    font-family: 'Dancing Script', cursive;
    text-shadow: 4px 2px 5px rgba(150, 150, 150, 0.69);
}

li {
    cursor: pointer;
}

li:hover {
    font-weight: bold;
    color: #EB9C19;
}


